* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.home {
}
.form-todo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(32, 33, 33);
  padding: 20px;
  border-radius: 10px;
}
.form-todo input {
  width: 100%;
  height: 2vw;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 10px;
}
.form-todo button {
  width: 100%;
  height: 2vw;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 10px;
}
.list-todo {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
#box {
  margin: 2vw;
  width: 20vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(0, 0, 0);
  padding: 20px;
  border-radius: 10px;
  color: white;
  transition: all ease-in 0.3s;
}
#box:hover {
  background-color: rgb(100, 102, 96);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.btn button {
  width: 100%;
  height: 2vw;
  padding-left: 10px;
  padding-right: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 10px;
}

@media (max-width: 768px) {
  .form-todo {
    width: 100%;
  }

  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(32, 33, 33);
    padding: 20px;
    border-radius: 10px;
  }
  .form-todo input {
    width: 100%;
    height: 4vh;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 10px;
  }
  .form-todo button {
    width: 100%;
    height: 4vh;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 10px;
  }
  .list-todo {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
  }
  #box {
    width: 100%;
    margin: 2vw;
  }
  #box:hover {
    background-color: rgb(100, 102, 96);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  }
  .btn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
  .btn button {
    width: 100%;
    height: 4vh;
    padding-left: 10px;
    padding-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 10px;
  }
}
